.datos-agua {
  margin-top: 30px;
}

.datos-agua h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.grafico-agua {
  width: 100%;
  height: 300px;
  margin-bottom: 20px;
}

.tabla-agua {
  margin-bottom: 20px;
}

.tabla-agua table {
  width: 100%;
  border-collapse: collapse;
}

.tabla-agua th,
.tabla-agua td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ccc;
}

.recomendaciones-agua {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 4px;
}

.recomendaciones-agua h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.recomendaciones-agua ul {
  list-style-type: disc;
  padding-left: 20px;
}

.recomendaciones-agua li {
  margin-bottom: 10px;
}